<template>
  <dialoghead :title="''" :open.sync="thatOpen" :buttonclick="submit" buttonicon="done">
    <mu-form :model="form" label-position="right" label-width="68">
      <mu-form-item prop="input" label="">
        <mu-text-field v-model="form.money" type="number"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="select" label="">
        <mu-auto-complete v-model="form.type" :data="consumes" :max-search-results="8" open-on-focus label-float></mu-auto-complete>
      </mu-form-item>
      <mu-form-item prop="date" label="">
        <mu-radio v-model="form.mode" value="" label=""></mu-radio>
        <mu-radio v-model="form.mode" value="" label=""></mu-radio>
        <mu-radio v-model="form.mode" value="" label=""></mu-radio>
      </mu-form-item>
      <mu-form-item prop="radio" label="">
        <mu-radio v-model="form.channel" :value="item.content" :label="item.content" v-for="item in currentUser.channels" :key="item.content"></mu-radio>
      </mu-form-item>
      <mu-form-item prop="checkbox" label="">
        <mu-date-input v-model="form.ttime" type="dateTime" actions></mu-date-input>
      </mu-form-item>
      <mu-form-item prop="switch" label="">
        <mu-text-field v-model="form.remark"></mu-text-field>
      </mu-form-item>
    </mu-form>
    <div style="text-align: right;margin-right: 10px;margin-top: 10px;">
      <mu-button color="red" @click="openAlert=true">
        
        <mu-icon right value="delete"></mu-icon>
      </mu-button>
    </div>
    <mu-dialog title=" ?" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="openAlert">
      ,!
      <mu-button slot="actions" flat color="primary" @click="openAlert=false"></mu-button>
      <mu-button slot="actions" flat color="primary" @click="del()"></mu-button>
    </mu-dialog>
  </dialoghead>
</template>

<script>
import dialoghead from "@/layout/dialog";
import { mapState } from "vuex";

export default {
  props: {
    open: {
      type: Boolean
    },
    currentItem: {
      type: Object
    }
  },
  components: {
    dialoghead
  },
  data() {
    return {
      thatOpen: false,
      consumes: [],
      labelPosition: "top",
      form: {
        id: 0,
        money: 0,
        type: "",
        mode: "",
        channel: "",
        ctime: "",
        remark: ""
      },
      thatSetInterval: 0,
      openAlert: false
    };
  },
  methods: {
    submit() {
      var that = this;
      var loading = that.$loading({});
      that.form.money = parseFloat(that.form.money);
      that.$axios.post("/tally/set", that.form).then(response => {
        if (response.code == 0) {
          // that.$toast.success("");
          // 
          that.currentItem.money = that.form.money;
          that.currentItem.type = that.form.type;
          that.currentItem.mode = that.form.mode;
          that.currentItem.ttime = that.form.ttime;
          that.currentItem.remark = that.form.remark;
          // 
          setTimeout(() => {
            if (that.thatSetInterval != 0) {
              clearInterval(that.thatSetInterval);
            }
            that.form = {
              id: 0,
              money: 0,
              type: "",
              mode: "",
              channel: "",
              ctime: "",
              remark: ""
            };
            that.thatOpen = false;
          }, 800);
        }
        loading.close();
      });
    },
    del() {
      var that = this;
      var loading = that.$loading({});
      that.$axios
        .get(`/tally/delete/${that.form.id}`)
        .then(response => {
          if (response.code == 0) {
            // that.$toast.success("");
            that.openAlert = false;
            that.currentItem.tid = "";
            setTimeout(() => {
              that.thatOpen = false;
            }, 300);
          }
          loading.close();
        })
        .catch(() => {
          loading.close();
        });
    }
  },
  watch: {
    currentUser(val) {
      if (!val) return;
      this.consumes = this.$linq(val.consumes)
        .select(x => x.content)
        .toArray();
    },
    open(val) {
      if (val) this.thatOpen = true;
    },
    thatOpen(val) {
      if (!val) {
        this.$emit("update:currentItem", {});
        this.$emit("update:open", false);
      }
    },
    currentItem(val) {
      if (!val.money) return;
      this.form = {
        id: val.tid,
        money: val.money,
        type: val.type,
        mode: val.mode,
        channel: val.channel,
        ttime: val.ttime,
        remark: val.remark
      };
    },
    form(val) {
      if (!val.money) return;
      var flag = val.mode == "" ? "" : "";
      var def = `${val.type}${val.mode}${val.money}${
        val.channel
      }${flag}`;
      if (def == val.remark) {
        this.thatSetInterval = setInterval(() => {
          var flag = val.mode == "" ? "" : "";
          val.remark = `${val.type}${val.mode}${val.money}${
            val.channel
          }${flag}`;
        }, 800);
      }
    }
  },
  computed: {
    ...mapState(["currentUser"])
  }
};
</script>

<style>
</style>




